body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  color: #ffffff;
  text-align: center; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.about {
  padding-bottom: 51px; }
  @media (max-width: 1199px) {
    .about {
      padding-bottom: 82px; } }
  @media (max-width: 767px) {
    .about {
      padding-bottom: 80px; } }

.about__wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 35.66%;
  margin-top: 117px;
  margin-right: 5.4%; }
  @media (max-width: 1199px) {
    .about__wrap {
      width: 47.60%;
      margin-top: 56px;
      margin-right: 4.44%; } }
  @media (max-width: 767px) {
    .about__wrap {
      width: 290px;
      margin: 0 auto; } }

.about__wrap span {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: #102654;
  text-transform: uppercase;
  margin-bottom: 15px;
  color: #ffffff; }
  @media (max-width: 1199px) {
    .about__wrap span {
      font-size: 34px;
      line-height: 34px;
      margin-bottom: 10px; } }
  @media (max-width: 767px) {
    .about__wrap span {
      font-size: 28px;
      line-height: 28px; } }

.about__wrap h2 {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 49px;
  line-height: 49px;
  letter-spacing: 0.03em;
  color: #102654;
  text-transform: uppercase;
  margin-bottom: 58px;
  font-size: 109px;
  line-height: 109px;
  text-align: right;
  font-weight: bold;
  color: #ffffff; }
  @media (max-width: 1199px) {
    .about__wrap h2 {
      font-size: 86px;
      line-height: 86px;
      margin-bottom: 45px; } }
  @media (max-width: 767px) {
    .about__wrap h2 {
      font-size: 78px;
      line-height: 78px; } }

.about__place {
  position: absolute;
  top: 188px;
  right: 265px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  @media (max-width: 1199px) {
    .about__place {
      top: 130px;
      right: 215px; } }
  @media (max-width: 767px) {
    .about__place {
      top: 120px;
      right: 175px; } }

.about__place p {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  text-align: right;
  white-space: nowrap; }

@media (max-width: 1199px) {
  .about__img {
    width: 324px;
    height: 390px; } }

@media (max-width: 767px) {
  .about__img {
    display: none; } }

.about__social {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: auto;
  margin-bottom: 45px;
  margin-left: auto; }
  @media (max-width: 1199px) {
    .about__social {
      display: none; } }

.about__item {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  margin-bottom: 15px;
  line-height: 0; }
  .about__item:last-child {
    margin-bottom: 0; }

.about__link {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: #ffffff; }

.button {
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
  box-sizing: border-box;
  padding: 24px 57px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid #ffffff; }

.button--member {
  padding: 24px 56px;
  border: 4px solid #ffffff; }
  @media (max-width: 1199px) {
    .button--member {
      padding: 22px 31px; } }
  @media (max-width: 767px) {
    .button--member {
      padding: 20px 82px; } }

.container {
  max-width: 1200px;
  padding-right: 45px;
  padding-left: 45px;
  margin: auto; }
  @media (max-width: 767px) {
    .container {
      padding-right: 15px;
      padding-left: 15px; } }

.page-header {
  position: relative;
  background-color: red;
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover; }

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px; }
  @media (max-width: 1199px) {
    .page-header__wrapper {
      padding-top: 40px;
      margin-bottom: 25px; } }
  @media (max-width: 767px) {
    .page-header__wrapper {
      padding-top: 20px;
      margin-bottom: 80px; } }

.page-header__logo img {
  width: 89px;
  height: 20px; }
  @media (max-width: 1199px) {
    .page-header__logo img {
      width: 82px;
      height: 17px; } }
  @media (max-width: 767px) {
    .page-header__logo img {
      width: 68px;
      height: 15px; } }

.page-header__phone {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  color: #ffffff; }
  .page-header__phone:hover, .page-header__phone:focus {
    cursor: pointer; }
  @media (max-width: 1199px) {
    .page-header__phone {
      font-size: 24px;
      line-height: 24px;
      letter-spacing: 0.02em; } }
  @media (max-width: 767px) {
    .page-header__phone {
      font-size: 20px;
      line-height: 20px; } }

.page-header__about {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }
